.Life {
  max-width: 425px;
  .back {
    position: fixed;
    top: 0; left: 0;
    padding: 0 24px;
    height: 44px;
    width: 100%;
    z-index: 2;
    background: #fff;
    box-shadow: -3px -2px 2px 2px #ccc;
    .back_icon {
      height: 44px;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }
  .inner {
    position: relative;
    top: 74px; left: 0;
    padding: 0 24px;
    h1 {
      font-size: 20px;
      font-weight: 700;
    }
    .tab {
      ul {
        margin-top: 15px;
        display: flex;
        li.active {
          width: 70px;
          height: 25px;
          line-height: 25px;
          border: 1px solid #F7786B;
          background: #F7786B;
          color: #fff;
          border-radius: 15px;
          text-align: center;
          margin-right: 10px;
        }
        li.default {
          width: 70px;
          height: 25px;
          line-height: 25px;
          border: 1px solid #ccc;
          color: #ccc;
          border-radius: 15px;
          text-align: center;
          margin-right: 10px;
        }
      }
    }
  }
}