.Main {
  max-width: 425px;
  height: 130vh;
  .back {
    position: fixed;
    top: 0; left: 0;
    padding: 0 24px;
    height: 44px;
    width: 100%;
    z-index: 2;
    background: #fff;
    box-shadow: -3px -2px 2px 2px #ccc;
    .back_icon {
      height: 44px;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }
  .inner {
    height: 130vh;
    margin-top: 44px;
    padding-bottom: 50px;
    padding: 0 24px;
    background: #f4f6f8;
    h1 {
      padding: 30px 0;
      font-size: 20px;
      font-weight: 700;
    }
    .bg {
      background: #F7786B;
      height: 330px;
      padding: 10px;
      display: flex;
      border-radius: 10px;
      .box {
        background: #fff;
        border-radius: 10px;
      }
      .box1 {
        flex: 25%;
        display: flex;
        flex-direction: column;
        margin-right: 10px;
        .box01 {
          margin-bottom: 10px;
          height: 165px;
          background: #aee5ff;
        }
        .box02 {
          margin-bottom: 10px;
          height: 77.5px;
          background: #ffcccc;
        }
        .box03 {
          height: 77.5px;
        }
      }
      .box2 {
        flex: 25%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin-right: 10px;
        .box2-1 {
          display: flex;
          flex-direction: column;
          align-items: flex-end;
          .box01 {
            width: 100%;
            height: 75.08px;
            margin-bottom: 10px;
          }
          .box02 {
            height: 75.08px;
            margin-bottom: 10px;
            width: 50%;
          }
        }
        .box03 {
          height: 75.08px;
        }
      }
      .box3 {
        flex: 25%;
        display: flex;
        flex-direction: column;
        margin-right: 10px;
        .box01 {
          margin-bottom: 10px;
          height: 77.5px;
        }
        .box02 {
          margin-bottom: 10px;
          height: 165px;
          background: #ffcccc;
        }
        .box03 {
          height: 77.5px;
        }
      }
      .box4 {
        flex: 25%;
        display: flex;
        flex-direction: column;
        .box01 {
          margin-bottom: 10px;
          height: 77.5px;
          background: #ffcccc;
        }
        .box02 {
          margin-bottom: 10px;
          height: 77.5px;
        }
        .box03 {
          margin-bottom: 10px;
          height: 77.5px;
        }
        .box04 {
          height: 77.5px;
        }
      }
    }
    .Description {
      display: flex;
      .Description01 {
        width: 20px;
        height: 20px;
        background: #000;
        margin-right: 10px;
      }
      .Description02 {
        width: 20px;
        height: 20px;
        background: #000;
        margin-right: 10px;
      }
      .Description03 {
        width: 20px;
        height: 20px;
