.Pick {
  max-width: 425px;
  height: 89vh;
  position: relative;
  top: 0; left: 0;
  .back {
    position: fixed;
    top: 0; left: 0;
    padding: 0 24px;
    height: 44px;
    width: 100%;
    z-index: 2;
    background: #fff;
    box-shadow: -3px -2px 2px 2px #ccc;
    .back_icon {
      height: 44px;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }
  .tab {
    margin-top: 64px;
    .tab_inner {
      width: 45px;
      justify-content: space-between;
      display: flex;
      align-items: center;
      margin: 0 auto;
      .tab01 {
        width: 7.5px;
        height: 7.5px;
        background: #F7786B;
        border-radius: 15px;
      }
      .tab02 {
        width: 7.5px;
        height: 7.5px;
        background: #ccc;
        border-radius: 15px;
      }
      .tab03 {
        width: 7.5px;
        height: 7.5px;
        background: #ccc;
        border-radius: 15px;
      }
    }
  }
  .tab2 {
    height: 30px;
    h1 {
      padding: 0 24px;
      p {
        text-align: center;
        padding: 20px 0;
        font-size: 20px;
        font-weight: 700;
      }
    }
    ul {
      padding: 0 24px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      li {
        text-align: center;
        height: 90px;
        width: 90px;
        border: 1px solid #ccc;
        box-sizing: border-box;
        border-radius: 10px;
        transition: .5s;
        box-shadow: 2px 2px 2px #eeeeee;
        margin-bottom: 20px;
        padding-top: 20px;
        &:hover {
          border: 1px solid #F7786B;
        }
        p {
          margin-top: 5px;
        }
      }
    }
  }
  .btn {
    position: fixed;
    bottom: 50px; left: 0;
    z-index: 1;
    width: 100%;
    background: #fff;
    height: 44px;
    padding: 10px 0;
    .btn_inner {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 0 24px;
      h1 {
        text-align: center;
        width: 145px;
        height: 44px;
        background: #F7786B;
        color: #fff;
        font-weight: 700;
        line-height: 44px;
        border-radius: 10px;
      }
    }
  }
}