.UserTab {
  max-width: 425px;
  height: 93.5vh;
  .inner {
    height: 90vh;
    padding: 24px 24px;
    background: #f4f6f8;
    margin-bottom: -24px;
    h1 {
      font-size: 20px;
      font-weight: 700;
      padding-bottom: 24px;
    }
    .tab01 {
      width: 100%;
      height: 100px;
      background: #fff;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 10px;
      .icon {
        width: 50px;
        height: 50px;
        background: #ccc;
        border-radius: 50px;
        margin-left: -55px;
      }
      h1 {
        margin-left: 15px;
        font-size: 16px;
        font-weight: normal;
        padding: 0;
      }
    }
    .life {
      background: #fff;
      border-radius: 10px;
      width: 100%;
      height: 100px;
      margin-top: 15px;
      display: flex;
      .writing {
        flex: 33%;
        height: 80px;
        background: #fff;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 10px;
        border-radius: 10px 0 0 10px;
        border-right: 1px solid #ddd;
        p {
          margin-top: 5px;
        }
      }
      .heart {
        flex: 33%;
        height: 80px;
        background: #fff;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 10px;
        border-radius: 10px 0 0 10px;
        border-right: 1px solid #ddd;
        p {
          margin-top: 5px;
        }
      }
      .deal {
        flex: 33%;
        height: 100px;
        background: #fff;
        display: flex;
        flex-direction: column;
        align-items: center;
        border-radius: 10px;
        p {
          margin-top: 5px;
        }
      }
    }
  }
}