.Chat {
  max-width: 425px;
  .back {
    position: fixed;
    top: 0; left: 0;
    padding: 0 24px;
    height: 44px;
    width: 100%;
    z-index: 2;
    background: #fff;
    box-shadow: -3px -2px 2px 2px #ccc;
    .back_icon {
      height: 44px;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }
  ul {
    margin: 0 24px;
  }
}